import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  typeOfData: "",
  typeOfMapping: "",
  nextPage: "material", // current page name
  MMmappingData: {},
  bomMappingData: {
    id: { name: "ID", address: "A" },
    associated_product_code_bom: { name: "Ass Product code (BOM)", address: "B" },
  }, // mapping configurationdata for materil master

  mmExportFailedRows: null,
  mmExportSuccessRows: null,
  activeProject: null,
};

export const taskpaneSlice = createSlice({
  name: "taskpaneSlice",
  initialState,
  reducers: {
    setTypeOfData: (state, action) => {
      state.typeOfData = action.payload;
    },
    setNextPage: (state, action) => {
      state.nextPage = action.payload;
      localStorage.setItem("taskPanePage", action.payload);
    },
    setTypeOfMapping: (state, action) => {
      state.typeOfMapping = action.payload;
    },
    setMMmappingData: (state, action) => {
      state.MMmappingData = {
        ...state.MMmappingData,
        [action.payload.value]: {
          name: action.payload.name,
          address: action.payload.address,
          columnNumber: action.payload.columnNumber,
        },
      };
      localStorage.setItem("MMmappingData", JSON.stringify(state.MMmappingData));
    },
    setMMmappingData2: (state, action) => {
      state.MMmappingData = action.payload;
    },
    setBomMappingData: (state, action) => {
      state.bomMappingData = {
        ...state.bomMappingData,
        [action.payload.value]: {
          name: action.payload.name,
          address: action.payload.address,
          columnNumber: action.payload.columnNumber,
        },
      };
      localStorage.setItem("bomMappingData", JSON.stringify(state.bomMappingData));
    },
    setBomMappingData2: (state, action) => {
      state.bomMappingData = action.payload;
    },

    setmmExportFailedRows: (state, action) => {
      state.mmExportFailedRows = action.payload;
    },
    setmmExportSuccessRows: (state, action) => {
      state.mmExportSuccessRows = action.payload;
    },
    setActiveProject: (state, action) => {
      state.activeProject = action.payload;
    },
  },
});

export const {
  setTypeOfData,
  setNextPage,
  setTypeOfMapping,
  setMMmappingData,
  setMMmappingData2,
  setmmExportFailedRows,
  setmmExportSuccessRows,
  setActiveProject,
  setBomMappingData,
  setBomMappingData2,
} = taskpaneSlice.actions;

export default taskpaneSlice.reducer;
